import { Injectable } from '@angular/core';
import { Http, Headers } from '@angular/http';
import 'rxjs/add/operator/map';
import { tokenNotExpired } from 'angular2-jwt';
import { RootService } from './root.service';

@Injectable()
export class AuthService {
	authToken: any;
	user: any;
	root: String = this.rootService.root;

	constructor(
		private http: Http,
		private rootService: RootService
	) { }

	registerUser(user) {
		let headers = new Headers();
		headers.append('Content-Type','application/json');
		return this.http.post(this.root + 'users/register', user, {headers: headers})
			.map(res => res.json());
	}

	authenticateUser(user) {
		let headers = new Headers();
		headers.append('Content-Type','application/json');
		return this.http.post(this.root + 'users/authenticate', user, {headers: headers})
			.map(res => res.json());
	}

	getProfile() {
		let headers = new Headers();
		this.loadToken();
		headers.append('Authorization', this.authToken);
		headers.append('Content-Type','application/json');
		return this.http.get(this.root + 'users/profile', {headers: headers})
		  .map(res => res.json());
	}

	storeUserData(token, user) {
		localStorage.setItem('id_token', token);
		localStorage.setItem('user', JSON.stringify(user));
		this.authToken = token;
		this.user = user;
	}

	loadToken() {
		const token = localStorage.getItem('id_token');
		this.authToken = token;
	}

	loggedIn() {
		if('id_token') {
			return tokenNotExpired('id_token');
		}
	}

	logout() {
		this.authToken = null;
		this.user = null;
		localStorage.clear();
	}
}



